import React from 'react';
import ItemsList from './ItemsList';
import { Element } from 'react-scroll';

const Category = ({ name, items }) => {
  return (
    <div className="category px-6 sm:px-4 md:px-8 lg:px-64 xl:px-128 ">
      <Element name={'category__' + name}>
      <div className='z-10  sticky top-28  pb-4  bg-gradient-to-b from-white from-90% '>
        <h2 className="text-3xl font-bold font-newsreader pt-8">{name}</h2>
      </div>
      <ItemsList items={items} />
      </Element>
    </div>
  );
};

export default Category;