import './App.css';
import './i18n';
import { useTranslation } from 'react-i18next';
import Category from './components/Category';
import Header from './components/Header';
import Hero from './components/Hero';
import Footer from './components/Footer';
import Menu from './components/Menu';
import carta from './carta.json';
import { useEffect, useState } from 'react';


// ---------------------------------------------------------
const randomImage = () => {
  const images = [
    'https://images.unsplash.com/photo-1473093295043-cdd812d0e601?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D',
    'https://images.unsplash.com/photo-1484980972926-edee96e0960d?q=80&w=1974&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D',
    'https://images.unsplash.com/photo-1467003909585-2f8a72700288?w=600&auto=format&fit=crop&q=60&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8MzZ8fGZvb2R8ZW58MHx8MHx8fDA%3D',
    'https://images.unsplash.com/photo-1505253668822-42074d58a7c6?w=600&auto=format&fit=crop&q=60&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8NTV8fGZvb2R8ZW58MHx8MHx8fDA%3D',
  ];
  return images[Math.floor(Math.random() * images.length)];
}
for (const category in carta) {
  const items = carta[category];
  items.forEach((item) => {
    item.image = randomImage()
  });
}
// ---------------------------------------------------------^


function App() {
  const [scrollDirection, setScrollDirection] = useState(null); // "up" or "down"
  const [lastScrollY, setLastScrollY] = useState(0);


  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;

      if (currentScrollY > lastScrollY) {
        setScrollDirection("down");
      } else if (currentScrollY < lastScrollY) {
        setScrollDirection("up");
      }

      setLastScrollY(currentScrollY);
    };

    window.addEventListener("scroll", handleScroll);

    // Cleanup on component unmount
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [lastScrollY]);


  return (
    <div className="App">
      <Hero />
      <div className={'w-full  sticky top-0 z-20 bg-white ' + `transition-all duration-150 ${scrollDirection === 'down' ? '-translate-y-20' : 'translate-y-0'}`}>
        <Header />
        <Menu items={Object.keys(carta)} />
      </div>
      <div className='h-10 w-full bg-white'></div>
      <div className={`transition-all duration-150 ${scrollDirection === 'down' ? '-translate-y-20' : 'translate-y-0'}`}>
        {
          (() => {
            const categories = Object.keys(carta);
            return categories.map((category, index) => {
              const items = carta[category];


              return (
                <Category key={index} name={category} items={items} />
              );
            });
          })()
        }
      </div>
      <Footer />
    </div>
  );
}

export default App;