import React, { useState } from "react";
import { Dropdown, Image, Space } from 'antd';
import i18n from "../i18n";
import LanguageIconOutlined from '@mui/icons-material/LanguageOutlined';
import ShareOutlinedIcon from '@mui/icons-material/ShareOutlined';
// Logo-CV.png
import logoCV from '../Logo-CV.png';

function Header() {
  const [current, setCurrent] = useState('language');
  const onClick = (e) => {
    switch (e.key) {
      case 'english':
        i18n.changeLanguage('en');
        break;
      case 'spanish':
        i18n.changeLanguage('es');
        break;
      default:
        break;
    }
    setCurrent(e.key);
  };

  const shareMenuItems = [
    {
      label: (
        <a target="_blank" rel="noopener noreferrer" href="https://www.antgroup.com">
          1st menu item
        </a>
      ),
      key: '0',
    },
    {
      label: (
        <a target="_blank" rel="noopener noreferrer" href="https://www.aliyun.com">
          2nd menu item
        </a>
      ),
      key: '1',
    },
    {
      type: 'divider',
    },
    {
      label: '3rd menu itemdisabled',
      key: '3',
      disabled: true,
    },
  ];

  const languageMenuItems = [
    {
      label: 'English',
      key: 'english',
    },
    {
      label: 'Espaol',
      key: 'spanish',
    },
  ]



  return (
    <header className="flex items-center justify-between h-20 ">
      <section className="flex-1 flex justify-center items-center">
        <Image src={logoCV} width={120} preview={false} />
      </section>
      <section className="flex gap-2">
        <Dropdown
          menu={{
            items: languageMenuItems,
            onClick: onClick,
          }}
        >
          <a className="mr-4" onClick={(e) => e.preventDefault()}>
              <LanguageIconOutlined fontSize="small" />
          </a>
        </Dropdown>
        <Dropdown
          menu={{
            items: shareMenuItems,
          }}
        >
          <a className="mr-4" onClick={(e) => e.preventDefault()}>
              <ShareOutlinedIcon fontSize="small" />
          </a>
        </Dropdown>
      </section>
    </header>
  );
}







export default Header;