import React, { useState } from 'react';
import { Element, Link } from 'react-scroll';

const HorizontalMenu = ({ children }) => {
  return (
    <div
      className="w-full overflow-x-auto flex whitespace-nowrap hover:scrollbar-thin hover:scrollbar-thumb-gray-300 hover:scrollbar-track-transparent scroll-smooth"
      style={{
        WebkitOverflowScrolling: 'touch'
      }}
    >
      <div className="flex items-center space-x-6 px-4">
        {children}
      </div>
    </div>
  );
};

// Example usage component
/**
 * Menu component renders a horizontal menu with clickable items.
 *
 * @param {string[]} items - An array of strings representing the menu items.
 * @returns {JSX.Element} The rendered Menu component.
 */

const Menu = ({ items = ['Home'] }) => {
  const [activeItem, setActiveItem] = useState('Home');

  return (
    <HorizontalMenu>

      {items.map((item) => (
        <Link to={"category__" + item} spy={true} smooth={true} duration={500} offset={-80} activeClass='bg-slate-50 text-slate-900 font-semibold'>
          <button
            key={item}
            className={`px-4 py-2 rounded-md transition-all duration-200 text-slate-600 hover:text-slate-900 hover:bg-slate-50/50`}
          >

            {item}
          </button>
        </Link>
      ))}
    </HorizontalMenu>
  );
};

export default Menu;